import styled from 'styled-components'
import { BASE_COLOR } from '../vars'

export default styled.div`
  background: ${BASE_COLOR};
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
`



